.search
{
    margin:20px 50px 0;
    display:flex;
}

.search input
{
    padding:20px 10px 10px;
    border:0;
    width:100%;
    color:#999;
    font-size: 24px;
    text-align: center;
}

.search input:focus
{
    outline: none;
    border-bottom: 1px solid #CCC;
}

.btn
{
    width: 150px;
    padding: 6px 16px;
    background: rgb(0,91,151); 
    color: #FFF;
    border: 2px solid;
    font-weight: bold;
    border-radius: 6px;
    box-shadow: inset 1px 1px 4px 2px #FFF;
    cursor: pointer;

}

.radio
{
    margin:20px 50px 0;
    display:flex;
    justify-content: space-between;
}
.radio input
{
    width:20px;
    height:20px;
}
.radio span
{
    font-size: 24px;
    margin-right: 60px;
}

.navigator {
    display: flex;
    justify-content: center; /* Центрирует кнопки по горизонтали */
    }